import React from 'react';
import Logo from './Logo';
import Track from './Track';


const style = {
    div: {
        zIndex: 3,
        position: 'absolute',
        top: 0,
        width: '100%',
        backgroundColor: 'red',
    },

    logo: {
        width: '100%',
    },

    cover: {
        width: '45%',
        height: 'auto',
        display: 'inline-block',
        marginLeft: '4%',
        marginRight: '2%'
    },

    detail: {
        width: '45%',
        display: 'inline-block',
        marginRight: '4%'
    },

    tracks: {
        width: '92%',
        marginLeft: '4%'
    }
};


class VolView extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div style={style.div}>
                <div style={style.logo}><Logo/></div>
                <img src={this.props.cover || '../static/pic/5877de4c96b3d.jpg'} style={style.cover}/>
                <div style={style.detail}>
                    <span>{this.props.tag || '#...'}</span>
                    <h1>{this.props.title || ''}</h1>
                    <p>{this.props.description || 'Vol.679 Cover From Isaac Gautschi    '}</p>
                    <span>{this.props.time || '1995 / 10 / 05'}</span>
                </div>

                <div style={style.tracks}>
                    <Track/><Track/><Track/><Track/>
                </div>
            </div>
        )
    }
}


export default VolView;


